.SearchBar {
    padding-left: 30px;
    display: flex;
    border-radius: 100px;
    height: 30px;
    background-color:#F6F7F9;
    padding-top: 15px;
    width:70%;
    padding-bottom: 15px;
    align-items: center !important;
}

.SearchBar > input {
    border: none;
    font-size: 20px;
    font-weight: bold;
    color: #000;
    background: none;
    outline: none;
    width:70%;
    margin-left: 10px;
    align-items: center;

}

.TopBar {
    margin-top: 50px;
    padding-left : 40px;
    display: flex;
    flex:0.8;
    flex-wrap: wrap;
    justify-content: space-between;
}

.NotificationsContainer {
    padding-left: 20px;
    border-radius: 20px;
    background-color: #F6F7F9;
    height: 30px;
    width: 2.75%;
    align-items: center;
    padding-top: 15px;
    padding-bottom: 15px;
    cursor: pointer;
}


.ProfileClosedContainer >img {
    height:40px;
    border-radius: 50px;
    margin-right: 10px;
}

.ProfileClosedContainer{
    height:40px;
    padding: 10px;
    width: 100%;
    align-items: center;
    border-radius: 50px;
    background-color: #F6F7F9;
    display: flex;
    cursor: pointer;
}

.break {
    flex-basis: 100%;
    height: 0;
    color: red;
    display: block;
  }


  